* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.1rem;
}
body {
  padding: 60px;
}

input {
  font-size: 1.1rem;
  width: 100%;
  color: green;
}

.card-grid {
  display:flex;
  flex-direction:row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;

}

.card p {
  padding: 5px;
  text-align: center;
  font-size: .8rem;
}

.card img {
  max-height: 170px;
}